<template>
    <div class="black-bg" v-if="isOpen">
    <div class="white-bg">
        <img :src="objArr[idx].image">
        <h4>{{objArr[idx].title}}</h4>
        <p> {{month}} 개월 선택함 {{objArr[idx].price * parseInt(month)}}원</p>
        <p>{{comment}}</p>
        <p>{{option}}</p>
        <!-- <input @input="month = $event.target.value"> -->
        <input v-model.number="month">
        <input type="range" min="1" max="12">
        <textarea v-model="comment"></textarea>
        <select v-model="option">
          <option>주차가능</option>
          <option>반려동물가능</option>
          <option>엘리베이터</option>
        </select>
        <p>{{objArr[idx].content}}</p>
          <Discount/>
        <button @click="$emit('closeModal')">close</button>
    </div>
  </div>
</template>

<script>
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name : 'Modal',
    props : {
        objArr : Array,
        idx : Number,
        isOpen : Boolean,
    },
    data() {
      return {
        month: 1,
        comment : '',
        option : '',
      }
    },
    watch : {
      month(before) {
        if(before >= 13) alert('warnning');
        if(isNaN(before)) {
            alert('문자 입력하지 마셈');
           this.month = 1;
        } 

      }
    },
    updated() {
      if(this.month === 2) {
        alert('2개월은 안됨');
        this.month = 1;
      }
    }
    
}
</script>

