<template>
<Transition name="fade">
<!-- <div class="start" :class="{end: isOpen}"> -->
  <Modal :objArr="objArr" :idx="idx" :isOpen="isOpen" @closeModal="isOpen = false;"/>
<!-- </div> -->
</Transition>
  <div class="menu">
    <a v-for="item in menu" :key="item"> {{item}}</a>
  </div>
  <Discount :discount = "discount" />
  <!-- <Discount v-if="showDiscount" /> -->

  <button @click="priceSort">가격 순 정렬</button>
  <button @click="priceSortDesc">가격 역순 정렬</button>
  <button @click="nameSort">이름 순 정렬</button>
  <button @click="nameSortDesc">이름 역순 정렬</button>
  <button @click="sortBack">되돌리기</button>

  <Card @openModal="isOpen = true; idx = i;"  v-for="(data,i) in objArr" :key="data" :item="objArr[i]" />

  
</template>

<script>
import {objArr} from './data.js';
import Discount from './Discount.vue';
import Modal from './Modal.vue';
import Card from './Card.vue';
// let objArr = [
//   {product : '역삼동원룸', price:50, cnt : 0, img: './assets/img/a.jpg'},
//   {product : '천호동원룸', price:60, cnt : 0, img: './assets/img/b.jpg'},
//   {product : '합정동원룸', price:70, cnt : 0, img: './assets/img/c.jpg'}
// ];
let menu = ['Home', 'Products','About'];

export default {
  name: 'App',
  data() {
    return {
     objArr : objArr,
     objArrOrigin : [...objArr],
     menu   : menu,
     cnt : [0,0,0],
     isOpen : false,
     idx : 0,
     showDiscount : true,
     discount: 30,
    }
  },
  methods : {
    priceSort() {
      this.objArr.sort(function(a,b){return a.price-b.price;}) // 숫자정렬
    },
    priceSortDesc() {
      this.objArr.sort((a,b) => b.price-a.price);
    },
    nameSort(){
      this.objArr.sort((a,b) => {
        let x = a.title.toUpperCase();
        let y = b.title.toUpperCase();
        return x < y ? -1 : (x > y ? 1 : 0);
      });
    },
    nameSortDesc(){
      this.objArr.sort((a,b) => {
        let v = a.title.toUpperCase();
        let w = b.title.toUpperCase();
        return w < v ? -1 : (w > v ? 1 : 0);
      });
    },
    sortBack(){
      this.objArr = [...this.objArrOrigin];
    }
  },
mounted(){
    // setTimeout(() => this.showDiscount = false, 2000);
    setInterval(() => {
        if(this.discount > 0) this.discount--;
        else return;
    
    },1000);
  },
  components: {
    Discount : Discount,
    Modal   : Modal,
    Card    : Card,
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.menu {
  background-color: darkslateblue;
  padding:15px;
  border-radius: 5px;
}
.menu a {
  color:#fff;
  padding: 10px;
}
img {
  width:100%;
  margin-top: 40px;
}

.black-bg {
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  position: fixed;
  padding:20px;
}
.white-bg {
  width: 100%;
  background: #fff;
  border-radius: 8px;
  padding: 20px;
}
.discount {
  background-color: #ccc;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
}

.start {
  opacity: 0;
  transition: all 1s;
}
.end {
  opacity: 1;
}

.fade-enter-from {transform:translateY(1000px)}
.fade-enter-active {transition: 1s;}
.fade-enter-to {transform:translateY(0px)}
.fade-leave-from {opacity: 1;}
.fade-leave-active {transition: 1s;}
.fade-leave-to {opacity: 0;}

</style>
